/* Main app stylesheet. We use the BEM methodology for modular CSS.
 * For more information, visit: http://getbem.com/. We use the "b"
 * namespace for block elements
 */
@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@keyframes fadeOut {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}
.b-body {
  overflow-x: hidden;
  background-color: #fff;
}
